import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
  },
  mutations: {
  },
  actions: {
    async contactUs ({ commit }, request) {
      try {
        const response = await axios.post('/contact', request);
        return response;
      } catch (e) {
        console.log(e);
      }
    }
  },
  modules: {
  }
})
