
















































import { menu } from "@/router";
import { Component, Vue } from 'vue-property-decorator';

@Component({
  components: {},
})
export default class Header extends Vue {
  routes = menu;
  instagram = '';
  loading = false;

  toggleLanguage () {
    this.loading = true;

    setTimeout(() => {
      this.loading = false;
    }, 3000)
  }
}
